export default class MailingService {

	sendRecruitForm(html) {
		return $.ajax('/v3/sandbox6260c1f608df4c3983c197598ec65cb9.mailgun.org/messages',
			{
				type: "POST",
				beforeSend: function (xhr) { xhr.setRequestHeader('Authorization', 'Basic YXBpOmtleS0yMWE2NDBhM2FiMTM5M2RmM2Q5MTI0ZTMyMjViMmYwMA=='); },
				data: {
					"html": html,
					"subject": 'Formoularz zgłoszeniowy',
					"from": "Fromularz rekrutacyjny <ulubiony@zlobek.pl>",
					"to": "ulubiony.zlobek@gmail.com"
				}
			});
	}
}


