<template>
 <div class="content">
  <navigation use-only-small-brand="true"></navigation>
  <div class="container pull-down-2">
    <div v-html="page.content"></div>
  </div>
 </div>
</template>

<script>
  import CmsService from "@/services/cmsApi.service.js";
  import Utils from "@/services/utils.service.js";
  import VueRouter from 'vue-router'
  import Navigation from "@/components/Navigation"

  const cmsService = new CmsService();
  const utils = new Utils();

  export default {
    name: 'Page',
    components: {Navigation},
    data: function () {
      return {
        page: {
          title: "",
          content: ""
        }
      }
    },
    mounted: function () {
      this.getPage();
    },
   watch: {
    '$route' (to, from) {
       this.getPage();
    }},
    methods: {
      getPage() {
        var self = this;
        var pageName = this.$route.params.pageName
        cmsService.getPageByPageName(pageName).then(page => {
          console.log(page)
          var newData = {
            title: page.title,
            content: page.content
          }
          self.page = newData;
           this.$nextTick(function () {
              utils.adjustFooter(window, document);
            })
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import "../css/_variables.scss";
</style>



