<template>
    <div class="content">
        <navigation use-only-small-brand="true"></navigation>
        <div class="container pull-down-2">
            <div class="row">
                <h1>{{album.title}}</h1>
            </div>
            <div class="row">
                <div class="album">
                    <div class="" v-for="(photo, index) in photos">
                        <div>
                            <a v-bind:src="photo.src" itemprop="contentUrl" @click="openPhotoswipe(index)">
                                <img class="photo-thumbnail" v-bind:src="photo.thumbnail_url" itemprop="thumbnail" alt="Image description" />
                            </a>
                        </div>
                    </div>
                </div>
    
            </div>
            <!-- Root element of PhotoSwipe. Must have class pswp. -->
            <div class="pswp" tabindex="-1" role="dialog" aria-hidden="true">
    
                <!-- Background of PhotoSwipe. 
                                     It's a separate element as animating opacity is faster than rgba(). -->
                <div class="pswp__bg"></div>
    
                <!-- Slides wrapper with overflow:hidden. -->
                <div class="pswp__scroll-wrap">
    
                    <!-- Container that holds slides. 
                                        PhotoSwipe keeps only 3 of them in the DOM to save memory.
                                        Don't modify these 3 pswp__item elements, data is added later on. -->
                    <div class="pswp__container">
                        <div class="pswp__item"></div>
                        <div class="pswp__item"></div>
                        <div class="pswp__item"></div>
                    </div>
    
                    <!-- Default (PhotoSwipeUI_Default) interface on top of sliding area. Can be changed. -->
                    <div class="pswp__ui pswp__ui--hidden">
    
                        <div class="pswp__top-bar">
    
                            <!--  Controls are self-explanatory. Order can be changed. -->
    
                            <div class="pswp__counter"></div>
    
                            <button class="pswp__button pswp__button--close" title="Close (Esc)"></button>
    
                            <button class="pswp__button pswp__button--share" title="Share"></button>
    
                            <button class="pswp__button pswp__button--fs" title="Toggle fullscreen"></button>
    
                            <button class="pswp__button pswp__button--zoom" title="Zoom in/out"></button>
    
                            <!-- Preloader demo http://codepen.io/dimsemenov/pen/yyBWoR -->
                            <!-- element will get class pswp__preloader--active when preloader is running -->
                            <div class="pswp__preloader">
                                <div class="pswp__preloader__icn">
                                    <div class="pswp__preloader__cut">
                                        <div class="pswp__preloader__donut"></div>
                                    </div>
                                </div>
                            </div>
                        </div>
    
                        <div class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap">
                            <div class="pswp__share-tooltip"></div>
                        </div>
    
                        <button class="pswp__button pswp__button--arrow--left" title="Previous (arrow left)">
                        </button>
    
                        <button class="pswp__button pswp__button--arrow--right" title="Next (arrow right)">
                        </button>
    
                        <div class="pswp__caption">
                            <div class="pswp__caption__center"></div>
                        </div>
    
                    </div>
    
                </div>
    
            </div>
        </div>
    </div>
</template>

<script>
import CmsService from "@/services/cmsApi.service.js";
import VueRouter from 'vue-router'
import Navigation from "@/components/Navigation"
import Utils from "@/services/utils.service.js";
import  PhotoSwipe from '../../node_modules/photoswipe/dist/photoswipe.js'
import  PhotoSwipeDefaultUI from '../../node_modules/photoswipe/dist/photoswipe-ui-default.js'
import '../../node_modules/photoswipe/dist/photoswipe.css'
import '../../node_modules/photoswipe/dist/default-skin/default-skin.css'
const cmsService = new CmsService();
const utils = new Utils();
export default {
    name: 'Album',
    components: { Navigation },
    data: function () {
        return {
            album: {
                title: "",
            },
            photos: []
        }
    },
    mounted: function () {
        this.getAlbum();
    },
    methods: {
        getAlbum() {
            var self = this;
            var id = this.$route.params.id
            cmsService.getAlbumById(id).then(album => {
                self.album = album;
                self.photos = album.album.rows.map(x => {
                    var mapped = {
                        id: x.id,
                        src: cmsService.getThumbnailUrl(x.data.url),
                        w: x.data.width,
                        h: x.data.height,
                        thumbnail_url: cmsService.getThumbnailUrl(x.data.thumbnail_url)
                    }
                    return mapped;
                });

                self.$nextTick(function () {
                    utils.adjustFooter(window, document);

                })
            });
        },
        openPhotoswipe(index) {
            this.open(index, this.photos)
        },
        open(index, items, options = {
            // captionEl: false,
            fullscreenEl: true,
            history: false,
            shareEl: false,
            tapToClose: true
        }) {
            const opts = Object.assign({
                index: index,
                getThumbBoundsFn(index) {
                    const thumbnail = document.querySelectorAll('.photo-thumbnail')[index]
                    const pageYScroll = window.pageYScroll || document.documentElement.scrollTop
                    const rect = thumbnail.getBoundingClientRect()
                    return {
                        x: rect.left,
                        y: rect.top + pageYScroll,
                        w: rect.width
                    }
                }
            }, options)
            this.photoswipe = new PhotoSwipe(document.querySelectorAll('.pswp')[0], PhotoSwipeDefaultUI, items, opts)
            this.photoswipe.init()
        },
        close() {
            this.photoswipe.close()
        }
    }
}

</script>
<style lang="scss">
@import "../css/_variables.scss";
.album {
    display: flex;
}

.photo-thumbnail {
    width: 200px;
    height: 200px;
    margin: 10px;
    padding: 5px;
    background-color: $primary-yellow;
}
</style>



