<template>
  <div class="container pull-down-half">
      <div class="row">
        <div class="news-box" v-for="post in blogPosts"> 
          <div class="col-md-3 photo-container">
            <a href="#">
              <img v-show="post.thumbnailUrl" class="post-preview__thumbnail" v-bind:src="post.thumbnailUrl" />
              <img v-show="!post.thumbnailUrl" class="post-preview__thumbnail" src="../assets/logo-color.png" />

            </a>
          </div>
          <div class="col-md-3">
            <h3  v-html="post.title"> </h3>
            <p  v-html="post.content"></p>
            <p>
              <a v-bind:href="'#/blog/'+post.id">Czytaj dalej</a>
            </p>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
  import CmsService from "@/services/cmsApi.service.js";
  const cmsService = new CmsService();
  export default {
    data(){
      return {
        blogPosts: []
      };
    },
    methods: {
      loadPosts() {
        cmsService.getTopTwoBlogPosts().then(x => this.blogPosts = x.map(post=>{
          var mapped = {
            id: post.id,
            title: post.title,
            content: post.content.replace(/<[^>]+>/g, '').substring(0,200)+'...',
            thumbnailUrl: post.thumbnail === null?null : 
              cmsService.getThumbnailUrl(post.thumbnail.thumbnail_url)
          }
          return mapped;
        }));
      }
    },
    mounted: function(){
      this.loadPosts();
    }
  }

</script>
<style scoped lang="scss">
  @import "../css/_variables.scss";
  @import "../../node_modules/bootstrap-sass/assets/stylesheets/bootstrap/variables";

  .post-preview__thumbnail {
  
    max-width:200px;
    margin-top: 30px;
  }
  
  .photo-container {
    text-align: right;
    @media(max-width: $screen-sm-max){
      text-align:center;
    }
  }

</style>




