export default class CmsService {

    constructor() {
        this.serverAddress = "http://api.ulubionyzlobek.pl/";
        this.baseApiUrl = "/_/";
        this.defaultThumbnailUrl = "../assets/header-1900-1.jpg";
    }

    getThumbnailUrl(thumbnailUrl) {
        return this.serverAddress + thumbnailUrl;
    }

    get(url, params) {
        return $.ajax({
            url: this.baseApiUrl + url,
            data: params,
            method: "GET"
        });
    }

    getBlogPostById(id){
        return this.get("items/Blog/"+id);
    }
   
    getTopTwoBlogPosts() {
        return this.get("items/Blog", {
            perPage: 2,
            status: 1,
            sort: "created_on",
            sort_order: "desc"
        }).then(x => x.data);
    }
    
    getBlogPosts(perPage,page) {
        return this.get("items/Blog", {
            limit: perPage,
            status: 1,
            offset: (page-1)*perPage,
            sort: "created_on",
            sort_order: "desc",
            meta: "total_count"
        });
    }

    getAlbums(perPage, page){
        return this.get("tables/Galleries/rows", {
            limit: perPage,
            status: 1,
            offset: (page-1)*perPage,
            sort: "id",
            sortOrder: "desc"
        });
    }

    getAlbumById(id){
         return this.get("tables/Galleries/rows/"+id);
    }

    mapAlbumToAlbumDto(album){
         var mapped = {
            id: album.id,
            title: album.title,
            thumbnailUrl: album.Thumbnail === null? this.defaultThumbnailUrl : 
              this.getThumbnailUrl(album.Thumbnail.thumbnail_url)
          };
          return mapped;
    }

    getPageByPageName(urlName) {
        return this.get("items/Pages",
            {
                "filter[url_name][eq]": urlName
            }).then(x => x.data[0]);
    }

    getMenuItems(){
        return this.get("items/TopMenu", {"status": "published"})
        .then(response => response.data.map(menuItem => ({
                id: menuItem.id,
                displayName: menuItem.displayname,
                url: menuItem.url,
                status:1
        })));
    }

    mapPostToPostDto(post){
         var mapped = {
            id: post.id,
            title: post.title,
            content: post.content,
            thumbnailUrl: post.thumbnail === null? this.defaultThumbnailUrl : 
              this.getThumbnailUrl(post.thumbnail.thumbnail_url),
            addedAt: post.created_on
          };
          return mapped;
    }

    mapPostToShortVersion(post){
         var mapped = {
            id: post.id,
            title: post.title,
            content: post.content.replace(/<[^>]+>/g, '').substring(0,200)+'...',
            thumbnailUrl: post.thumbnail === null? null : 
              this.getThumbnailUrl(post.thumbnail.thumbnail_url),
            addedAt: post.created_on
          };
          return mapped;
    }
}



