<template>
  <!-- Navigation -->
  <nav class="navbar navbar-fixed-top" role="navigation">
    <div class="container">
      <!-- Brand and toggle get grouped for better mobile display -->
      <div class="navbar-header">
        <button type="button" class="navbar-toggle" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1">
                <span class="sr-only">Pokaż menu</span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
        </button>
        <a class="navbar-brand" v-bind:class="{'navbar-brand-small': useOnlySmallBrand}" href="#">
          <img src="../assets/logo-color.png" />
        </a>
      </div>
      <!-- Collect the nav links, forms, and other content for toggling -->
      <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
        <transition-group class="nav navbar-nav" name="list" tag="ul">
          <li v-for="item in menuItems" v-bind:key="item.url">
            <a v-bind:href="item.url">{{item.displayName}}</a>
          </li>
        </transition-group>
      </div>
      <!-- /.navbar-collapse -->
    </div>
    <!-- /.container -->
  </nav>
</template>

<script>
  import { store } from "@/services/store.js"

  export default {
    props: ['useOnlySmallBrand'],
    name: 'navigation',
    computed: {
      menuItems: function () {
        return this.$store.getters.menuItems
      }
    },
    mounted: function () {
      var adjustLogo = function () {
          if ($(document).scrollTop() > 50) {
            $(".navbar-brand").addClass("navbar-brand-small");
          } else {
            $(".navbar-brand").removeClass("navbar-brand-small");
          }
        };

      if (!this.useOnlySmallBrand) {
        $(window).bind("scroll", adjustLogo);
      }
      else {
        $(window).unbind("scroll");
      }
    }

  }

</script>

<style lang="scss">
  @import "../css/_variables.scss";
  @import "../../node_modules/bootstrap-sass/assets/stylesheets/bootstrap/variables";
  nav {
    background-color: $primary-yellow;
    ul.nav {
      .list-enter-active {
        transition: all 0.5s;
        -webkit-transition: all 0.5s;
      }
      .list-enter,
      .list-leave-to {
        opacity: 0;
        transform: translateX(-30px);
      }
      a,
      a:hover,
      a:focus {
        color: $black;
        font-weight: bolder;
      }
      a:hover,
      a:active,
      a:focus {
        background-color: $orange;
      }
    }
    .icon-bar {
      background-color: black;
    }
  }
  
  .navbar-brand {
    padding: 0!important;
    img {
      max-width: 250px;
      -webkit-transition: all 0.4s;
      transition: all 0.4s;
      @media(max-width: $screen-sm-max) {
        max-width:100px;
      }
    }
  }
  
  .navbar-brand-small {
    img {
      -webkit-transition: all 0.4s;
      transition: all 0.4s;
      max-width: 100px;
    }
  }

</style>




