<template>
    <div class="content">
          <navigation use-only-small-brand="true"></navigation>
        <div class="container pull-down-2">
            <div class="row">
                <div class="col-md-12">
                    <h2>Karta zgłoszenia dziecka</h2>
                    <form v-if="form">
                        <div class="form-group">
                            <label for="firstAndLastName">Imię i nazwisko dziecka</label>
                            <input v-model="form.child.firstAndLastName" type="text" class="form-control" id="firstAndLastName">
                        </div>
                        <div class="form-group">
                            <label for="dateOfBirthAndPlaceOfBirth">Data i miejsce urodzenia</label>
                            <input v-model="form.child.dateOfBirthAndPlaceOfBirth" type="text" class="form-control" id="dateOfBirthAndPlaceOfBirth">
                        </div>
                        <div class="form-group">
                            <label for="pesel">Pesel</label>
                            <input v-model="form.child.pesel" type="text" class="form-control" id="pesel">
                        </div>
                        <div class="form-group">
                            <label for="pesel">Adres zamieszkania</label>
                            <input v-model="form.child.address1" type="text" class="form-control" id="address1">
                        </div>
                        <div class="form-group">
                            <label for="pesel">Adres zameldowania</label>
                            <input v-model="form.child.address2" type="text" class="form-control" id="address2">
                        </div>
                        <h3>Matka</h3>
                        <div class="form-group">
                            <label for="motherFirstAndLastName">Imię i nazwisko</label>
                            <input v-model="form.mother.firstAndLastName" type="text" class="form-control" id="motherFirstAndLastName">
                        </div>
                        <div class="form-group">
                            <label for="motherAddress">Adres zamieszkania</label>
                            <input v-model="form.mother.address" type="text" class="form-control" id="motherAddress">
                        </div>
                        <div class="form-group">
                            <label for="motherIdNumber">Seria i numer dokumentu tożsamości</label>
                            <input v-model="form.mother.idNumber" type="text" class="form-control" id="motherIdNumber">
                        </div>
                        <div class="form-group">
                            <label for="motherWorkAddress">Nazwa i adres zakładu pracy</label>
                            <input v-model="form.mother.workAddress" type="text" class="form-control" id="motherWorkAddress">
                        </div>
                        <div class="form-group">
                            <label for="motherPhoneNumber">Numer kontaktowy</label>
                            <input v-model="form.mother.phoneNumber" type="text" class="form-control" id="motherPhoneNumber">
                        </div>
                        <div class="form-group">
                            <label for="motherEmail">e-mail</label>
                            <input v-model="form.mother.email" type="text" class="form-control" id="motherEmail">
                        </div>
                        <h3>Ojciec</h3>
                        <div class="form-group">
                            <label for="fatherFirstAndLastName">Imię i nazwisko</label>
                            <input v-model="form.father.firstAndLastName" type="text" class="form-control" id="fatherFirstAndLastName">
                        </div>
                        <div class="form-group">
                            <label for="fatherAddress">Adres zamieszkania</label>
                            <input v-model="form.father.address" type="text" class="form-control" id="fatherAddress">
                        </div>
                        <div class="form-group">
                            <label for="fatherIdNumber">Seria i numer dokumentu tożsamości</label>
                            <input v-model="form.father.idNumber" type="text" class="form-control" id="fatherIdNumber">
                        </div>
                        <div class="form-group">
                            <label for="fatherWorkAddress">Nazwa i adres zakładu pracy</label>
                            <input v-model="form.father.workAddress" type="text" class="form-control" id="fatherWorkAddress">
                        </div>
                        <div class="form-group">
                            <label for="fatherPhoneNumber">Numer kontaktowy</label>
                            <input v-model="form.father.phoneNumber" type="text" class="form-control" id="fatherPhoneNumber">
                        </div>
                        <div class="form-group">
                            <label for="fatherEmail">e-mail</label>
                            <input v-model="form.father.email" type="text" class="form-control" id="fatherEmail">
                        </div>
                        <br>
                        <div class="form-group">
                            <label for="since">Dziecko będzie uczęszczało do ULubionego żłobka od dnia:</label>
                            <input v-model="form.since" type="text" class="form-control" id="since">
                        </div>
                        <div class="form-inline">
                            <label for="since">Deklaruję, że moje dziecko będzie przebywało w żłobku najczęściej w godzinach:</label>
                            <br/>
                            <div class="form-group">
                                <label>Od:</label>
                                <input v-model="form.hourForm" type="text" class="form-control" id="hourFrom">
                            </div>
                            <div class="form-group">
                                <label>Do:</label>
                                <input v-model="form.hourTo" type="text" class="form-control" id="hourFrom">
    
                            </div>
                        </div>
                        <h2>Dodatkowe ważne informacje o dziecko</h2>
                        <div class="form-group">
                            <label for="medical">Czy dziecko przebywa pod stałą kontrolą poradni specjalistycznej, jeśli tak to jakiej?</label>
                            <input v-model="form.medical" type="text" class="form-control" id="medical">
                        </div>
                        <div class="form-group">
                            <label for="diseases">Czy dziecko cierpi na przewlekłe choroby (jakie)?</label>
                            <input v-model="form.diseases" type="text" class="form-control" id="diseases">
                        </div>
                        <div class="form-group">
                            <label for="otherChildInfo">Inne uwagi o dziecku:</label>
                            <input v-model="form.otherChildInfo" type="text" class="form-control" id="otherChildInfo">
                        </div>
                        <div v-show="isSuccess" class="alert alert-success">
                            <strong>Dziękujemy!</strong> Wysyłanie formularza powiodło się.
                        </div>
                        <div v-show="isError" class="alert alert-danger">
                            <strong>Uwaga!</strong> Wysyłanie formularza nie powiodło się. Prosimy o kontakt telefoniczny.
                        </div>
                        <button :disabled="isSubmiting" type="submit" @click="submit()" class="btn btn-default">
                            <i v-if="isSubmiting" class="fa fa-spinner fa-spin fa-fw"></i>Wyślij formularz
                        </button>
                    </form>
                    <br/>
                </div>
            </div>
        </div>
        <div id="email-template" class="hidden">
            <h1>Karta zgłoszenia dziecka</h1>
            Imię i nazwisko: {{form.child.firstAndLastName}}
            <br/> Data i miejsce urodzenia: {{form.child.dateOfBirthAndPlaceOfBirth}}
            <br/> PESEL: {{form.child.dateOfBirthAndPlaceOfBirth}}
            <br/> Adres zamieszkania:{{form.child.address1}}
            <br/> Adres zameldowania: {{form.child.address2}}
            <br/>
            <h2>Podstawowe informacje</h2>
            <table width="100%">
                <tr>
                    <td>Matka</td>
                    <td>Ojciec</td>
                </tr>
                <tr>
    
                    <td>
                        <small>Imię i nazwisko</small>
                        <br/> {{form.mother.firstAndLastName}}
                    </td>
                    <td>
                        <small>Imię i nazwisko</small>
                        <br/> {{form.father.firstAndLastName}}
                    </td>
                </tr>
                <tr>
                    <td>
                        <small>Adres zamieszkania</small>
                        <br/> {{form.mother.address}}
                    </td>
                    <td>
                        <small>Adres zamieszkania</small>
                        <br/> {{form.father.address}}
                    </td>
                </tr>
                <tr>
                    <td>
                        <small>Seria i numer dokumentu tożsamości</small>
                        <br/> {{form.mother.idNumber}}
                    </td>
                    <td>
                        <small>Seria i numer dokumentu tożsamości</small>
                        <br/> {{form.father.idNumber}}
                    </td>
                </tr>
                <tr>
                    <td>
                        <small>Nazwa i adres zakładu pracy</small>
                        <br/> {{form.mother.workAddress}}
                    </td>
                    <td>
                        <small>Nazwa i adres zakładu pracy</small>
                        <br/> {{form.father.workAddress}}
                    </td>
                </tr>
                <tr>
                    <td>
                        <small>Telefon kontaktowy</small>
                        <br/> {{form.mother.phoneNumber}}
                    </td>
                    <td>
                        <small>Telefon kontaktowy</small>
                        <br/> {{form.father.phoneNumber}}
                    </td>
                </tr>
                <tr>
                    <td>
                        <small>e-mail</small>
                        <br/> {{form.mother.email}}
                    </td>
                    <td>
                        <small>e-mail</small>
                        <br/> {{form.father.email}}
                    </td>
                </tr>
            </table>
            <br/> Dziecko będzie uczęszczało do ULubionego żłobka od dnia: {{form.since}}
            <br/> Deklaruję, że moje dziecko będzie przebywało w żłobku najczęściej w godzinach: {{form.hourFrom}} do {{form.hourTo}}
            <h2>Dodatkowe ważne informacje o dziecko</h2>
            Czy dziecko przebywa pod stałą kontrolą poradni specjalistycznej, jeśli tak to jakiej?
            <br/> {{form.medical}}
            <br/> Czy dziecko cierpi na przewlekłe choroby (jakie)?
            <br/> {{form.diseases}}
            <br/> Inne uwagi o dziecku:
            <br/> {{form.otherChildInfo}}
        </div>
    </div>
</template>

<script>

import MailingService from "@/services/mailing.service.js";
import Navigation from "@/components/Navigation"

var malingService = new MailingService();
export default {
    name: 'RecruitForm',
   components: {
    Navigation
  },
    data() {
        return {
            form: {
                child: {},
                mother: {},
                father: {}
            },
            isSubmiting: false,
            isValid: true,
            isSuccess: false,
            isError: false
        }
    },
    methods: {
        submit() {
            this.isSubmiting = true;
            this.$forceUpdate();
            this.isError = false;
            this.isSuccess = false;
            var self = this;
            this.$nextTick(function() {
                var html = this.convertToHtml(self.form);
                var promise = malingService.sendRecruitForm(html);
                promise.done(function (r) {
                    self.isSuccess = true;
                }).fail(function (jqXHR, textStatus, errorThrown) {
                    self.isError = true;
                }).always(() => self.isSubmiting = false);
            })


        },
        convertToHtml() {
            return '<html><body>' + $('#email-template').html() + '</body></html>';
        }
    }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../css/_variables.scss";
</style>



