<template>
  <div id="app">

    <router-view></router-view>
    <footer-content></footer-content>
  </div>
</template>

<script>
  import {store} from "@/services/store.js"
  import CmsService from "@/services/cmsApi.service.js"
  import {SET_MENU_ITEMS}  from "@/services/store.js"
  import FooterContent from "@/components/Footer"

  const cmsService = new CmsService();


  export default {
    name: 'app',
    components:{FooterContent},
    mounted: function () {
      var self = this;
      cmsService.getMenuItems().then(menuItems => {
        self.$store.commit(SET_MENU_ITEMS, menuItems);
      })
    }
  }

</script>

<style lang="scss">
  #app{
      display: flex;
      flex-direction: column;
  }
  .content{
    flex-grow: 10000000;
  }
  body {
    font-family: 'Roboto', sans-serif !important;
    background-color: #FFFAFA !important;
    
  }
 
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  h7,
  li {
    font-family: 'Open Sans', sans-serif !important;
  }
  
  .pull-down-half {
    margin-top: 20px;
  }
  
  .pull-down {
    margin-top: 40px;
  }
  
  .pull-down-2 {
    margin-top: 80px;
  }
  
  .pull-down-4 {
    margin-top: 160px;
  }

</style>



