<template>
  <div class="content">
    <navigation use-only-small-brand="true"></navigation>
    <div class="container pull-down-2" v-if="blogPosts.length > 0">
      <div class="news-box" v-for="post in blogPosts" v-bind:key="post.id">
        <div class="photo-container">
          <a v-bind:href="'#/blog/'+post.id">
            <img v-show="post.thumbnailUrl" class="post-preview__thumbnail" v-bind:src="post.thumbnailUrl" />
            <img v-show="!post.thumbnailUrl" class="post-preview__thumbnail" src="../assets/logo-color.png" />
          </a>
        </div>
        <div class="news-box__content">
          <a v-bind:href="'#/blog/'+post.id">
            <h3> {{post.title}}</h3>
          </a>
          <p>
            <a v-bind:href="'#/blog/'+post.id" v-html="post.content" ></a>
          </p>
          <p>
            <a v-bind:href="'#/blog/'+post.id"><strong>Czytaj dalej</strong></a>
          </p>
        </div>
      </div>
      <div class="row">
        <div class="pager">
          <button class="btn btn-default" @click="prevPage()" :disabled="currentPage === 1">
            Poprzednia
          </button>
          <span>Strona {{currentPage}} z {{pagesCount}}</span>
          <button class="btn btn-default" @click="nextPage()" :disabled="currentPage === pagesCount">
            Następna
          </button>
        </div>
      </div>
    </div>
    <div class="pull-down-2" v-if="!blogPosts || blogPosts.length == 0">
      <h1>Na razie nasza lista aktualności jest pusta</h1>
    </div>
  </div>

</template>

<script>
  import CmsService from "@/services/cmsApi.service.js";
  import VueRouter from 'vue-router'
  import Navigation from "@/components/Navigation"
  import Utils from "@/services/utils.service.js";

  const cmsService = new CmsService();
  const utils = new Utils();

  export default {
    name: 'BlogList',
    components: {
      Navigation
    },
    data: function () {
      return {
        blogPosts: [],
        pagesCount: 1,
        currentPage: 1,
        perPage: 2
      }
    },
    methods: {
      fetchData: function () {
        cmsService.getBlogPosts(this.perPage, this.currentPage)
          .then(response => {
            if (response.meta.total_count > this.perPage) {
              let pagesCount = parseInt(response.Active / this.perPage);

              if (response.Active % this.perPage !== 0)
                pagesCount++;

              this.pagesCount = pagesCount;
            }

            this.blogPosts = response.data.map(post => cmsService.mapPostToShortVersion(post));
            this.$nextTick(function () {
              utils.adjustFooter(window, document);
            })
          })
      },
      nextPage: function () {
        this.currentPage++;
        this.fetchData();
      },
      prevPage: function () {
        this.currentPage--;
        this.fetchData();
      }
    },
    mounted: function () {
      this.fetchData();
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../css/_variables.scss";
  .news-box {
    background-color: rgba($primary-yellow, 0.5);
    margin: 20px;
    display: flex;
    a {
      color: inherit;
    }
    >* {
      margin: 20px;
    }
    .post-preview__thumbnail{
      max-width:200px;
      max-height: 200px;
    }
  }

</style>




