import Vue from 'vue';
import Router from 'vue-router';
import Start from '@/components/Start';
import Page from '@/components/Page';
import BlogList from '@/components/BlogList';
import BlogPost from '@/components/BlogPost';
import Gallery from '@/components/Gallery';
import Album from '@/components/Album';
import Recruit from '@/components/RecruitForm';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Start',
      component: Start
    },
    {
      path: '/strona/:pageName',
      name: 'strona',
      component: Page,
    },
    {
      path: '/blog/',
      name: 'blogList',
      component: BlogList,
    },
    {
      path: '/blog/:id',
      name: 'blog',
      component: BlogPost,
    },
    {
      path: '/galeria',
      name: 'galeria',
      component: Gallery
    },
      {
      path: '/gallery/:id',
      name: 'album',
      component: Album
    },
    {
      path:'/rekrutacja',
      name: "rekrutacja",
      component: Recruit
    }
  ]
})



