<template>
  <div class="content">
    <navigation use-only-small-brand="true"></navigation>
    <div class="container pull-down-2">
      <h1>Galeria zdjęć z naszego żłobka</h1>
      <div class="row">
        <div class="albums">
          <div class="album"  v-for="album in albums" v-bind:key="album.id">
            <a v-bind:href="'#/gallery/'+album.id">
              <img class="album__thumbnail" v-bind:src="album.thumbnailUrl" />
            </a>
             <a v-bind:href="'#/gallery/'+album.id">
              <h3>{{album.title}}</h3>
            </a>
            
          </div>
        </div>
      </div>
  
      <div class="row" v-show="!(pagesCount == 1)">
        <div class="pager">
          <button class="btn btn-default" @click="prevPage()" :disabled="currentPage === 1">
            Poprzednia
          </button>
          <span>Strona {{currentPage}} z {{pagesCount}}</span>
          <button class="btn btn-default" @click="nextPage()" :disabled="currentPage === pagesCount">
            Następna
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CmsService from "@/services/cmsApi.service.js";
import VueRouter from 'vue-router'
import Navigation from "@/components/Navigation"
import Utils from "@/services/utils.service.js";

const cmsService = new CmsService();
const utils = new Utils();

export default {
  name: 'Gallery',
  components: {
    Navigation
  },
  data: function () {
    return {
      albums: [],
      pagesCount: 1,
      currentPage: 1,
      perPage: 2
    }
  },
  methods: {
    fetchData: function () {
      cmsService.getAlbums(this.perPage, this.currentPage)
        .then(response => {
          if (response.Active > this.perPage) {
            let pagesCount = parseInt(response.Active / this.perPage);

            if (response.Active % this.perPage !== 0)
              pagesCount++;

            this.pagesCount = pagesCount;
          }

          this.albums = response.rows.map(album => cmsService.mapAlbumToAlbumDto(album));
          this.$nextTick(function () {
            utils.adjustFooter(window, document);
          })
        })
    },
    nextPage: function () {
      this.currentPage++;
      this.fetchData();
    },
    prevPage: function () {
      this.currentPage--;
      this.fetchData();
    }
  },
  mounted: function () {
    this.fetchData();
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../css/_variables.scss";
.albums {
  display: flex;
  flex-direction: row;

}

.album {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba($primary-yellow, 0.5);
  margin: 20px;

  a {
    color: inherit;
    img {
      margin: auto;
    display: block;
    }
  }
  >* {
    margin: 20px;
  }
}
</style>




